      z-index: 1;
      transition:
        transform 0.3s,
        background-color 0.3s;

      &:hover {
        background-color: var(--dark);
        transform: translateY(-0.1875rem);
      }
    }

    .social-img {
      height: 2rem;
      width: 2rem;
      display: block;
    }

    /* Navigation Links */
    .links {
      display: flex;
      flex-wrap: wrap;
      column-gap: calc(100 / 16 * 1rem);
    }

    .nav {
      padding: 0;
      margin: 0;
    }

    .nav-li {
      list-style: none;
      margin-bottom: calc(16 / 16 * 1rem);
      &:last-of-type {
        margin-bottom: 0;
      }
    }

    .nav-link {
      font-size: calc(16 / 16 * 1rem);
      text-decoration: none;
      line-height: 1.5em;
      color: var(--color-body-text-light);
      position: relative;

      &:before {
        /* underline */
        content: "";
        width: 0%;
        height: calc(2 / 16 * 1rem);
        background: var(--color-body-text-light);
        opacity: 1;
        position: absolute;
        display: block;
        bottom: calc(-2 / 16 * 1rem);
        left: 0;
        transition: width 0.3s;
      }

      &:hover {
        &:before {
          width: 100%;
        }
      }
    }

    /* Contact Links */
    .contact {
      margin: 0;
      padding: 0;
      width: 35%;
    }

    .contact-li {
      list-style: none;
      margin-bottom: calc(0 / 16 * 1rem);
      &:last-of-type {
        margin-bottom: 0;
      }
    }

    .contact-link {
      font-size: calc(16 / 16 * 1rem);
      text-decoration: none;
      line-height: 1.5em;
      text-align: left;
      color: var(--color-body-text-light);
      display: inline-block;

      &:hover {
        text-decoration: underline;
      }
    }

    .address {
      font-size: calc(16 / 16 * 1rem);
      margin-bottom: calc(20 / 16 * 1rem);
    }
  }
}

/* Tablet - 768px */

/* Small Desktop - 1024px */
@media only screen and (min-width: 64rem) {
  #footer {
    padding-top: calc(61 / 16 * 1rem);
    padding-bottom: calc(61 / 16 * 1rem);

    .container {
      max-width: calc(1110 / 16 * 1rem);
      height: calc(128 / 16 * 1rem);
      display: grid;
      gap: 0;
      grid-template-columns: 164px 116px 542px 272px;
      grid-template-rows: repeat(2, 1fr);
      grid-template-areas:
        "logo x links subscribe"
        "social x links copy";
    }

    .logo-container {
      width: auto;
      max-width: calc(305 / 16 * 1rem);
      grid-area: logo;
      margin: 0;
      align-self: start;
    }

    .text {
      width: 100%;
      grid-area: copy;
      align-self: flex-end;
      text-align: right;
    }

    .links {
      gap: 0;
      grid-area: links;
    }

    .nav {
      width: calc(255 / 16 * 1rem);
    }

    .nav-li {
      margin-bottom: 0.5rem;
    }

    .nav-link {
      line-height: 1.625em;
    }

    .contact {
      width: auto;
    }

    .social {
      gap: 1rem;
      grid-area: social;
      max-width: 100%;
      align-self: flex-end;
    }

    .social-link,
    .social-img {
      width: 1.25rem;
      height: 1.25rem;
    }
    .subscribe {
      display: flex;
      width: 100%;
      height: calc(44 / 16 * 1rem);
      grid-area: subscribe;
      align-self: start;
      input {
        width: 200px;
      }

      .button-solid {
        padding: 0.75rem 1.5rem;
        line-height: 1.45em;
      }
    }
  }
}
