afa; /* Gray 900 */\n\n  --color-body-text-light: #ffffff; /* White */\n\n  --color-header-text: #242d52; /* Blue 950 */\n  --color-header-text-light: #ffffff; /* White */\n\n  --color-highlight: #f33c3c; /* Red */\n  --color-highlight-light: #e5e9ea; /* Blue 50 */\n\n  --accent: #f33c3c; /* Red */\n\n  --color-gradient: linear-gradient (rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.6));\n\n  --dark: #0f1d30;\n  --secondary: #1d3557ff;\n\n  --header-font: \"Be Vietnam Pro\", Arial, Helvetica, sans-serif;\n  --body-font: \"Be Vietnam Pro\", Arial, Helvetica, sans-serif;\n\n  /* 13px - 16px */\n  --topper-font-size: clamp(0.8125rem, 1.6vw, 1rem);\n  /* 32px - 40px */\n  --header-font-size: clamp(2rem, 3.9vw, 2.5rem);\n  --body-font-size: 1rem;\n\n  --header-logo-scale-factor: 186; /* decreasing/increasing this value will decrease/increase max size of the header logo */\n  /* 60px - 100px top and bottom */\n  --section-padding: clamp(3.75rem, 7.82vw, 6.25rem) 1rem;\n}\n\nhtml,\nbody {\n  font-family: var(--body-font);\n  font-size: 100%; /* unsure why this is necessary */\n  color: var(--color-body-text);\n  position: relative;\n  overflow-x: hidden;\n}\n\n.bg-image,\n.bg-image-two {\n  position: absolute;\n  visibility: hidden;\n}\n\n@media (min-width: 48rem) {\n  .bg-image {\n    visibility: visible;\n    top: -13.5rem;\n    right: -10rem;\n  }\n\n  .bg-image-two {\n    visibility: visible;\n    top: 42%;\n    left: -35%;\n  }\n}\n\n/* Utility Classes */\n.title {\n  font-size: var(--header-font-size);\n  font-weight: 700;\n  line-height: 1.2em;\n  text-align: inherit;\n  max-width: 43.75rem;\n  margin: 0 0 1rem 0;\n  color: var(--color-header-text);\n  position: relative;\n}\n\n.text {\n  font-size: var(--bod-font-size);\n  line-height: 1.5em;\n  text-align: inherit;\n  width: 100%;\n  max-width: 40.625rem;\n  margin: 0;\n  color: var(--color-body-text);\n  opacity: 50.25%;\n}\n\n.bold {\n  font-weight: 700;\n}\n\n.hide-on-mobile {\n  display: none;\n}\n\n/* Desktop Utility Classes */\n@media only screen and (min-width: 64rem) {\n  .hide-on-mobile {\n    display: block;\n  }\n\n  .hide-on-desktop {\n    display: none;\n  }\n}\n\n/* Base Element Styles */\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  font-family: var(--header-font);\n  line-height: 1.2em;\n  color: var(--color-complementary);\n}\n\np,\nli,\na {\n  font-size: calc(16 / 16 * 1rem);\n  line-height: 1.5em;\n  color: var(--color-body-text);\n}\n\na,\nbutton {\n  &:hover {\n    cursor: pointer;\n  }\n}\n\nbody.dark-mode {\n  background-color: var(--dark);\n\n  p,\n  li,\n  h1,\n  h2,\n  h3,\n  h4,\n  h5,\n  h6 {\n    color: var(--color-body-text-light);\n  }\n}\n\n/* ******************* */\n/*        FONTS        */\n/* ******************* */\n\n/* be-vietnam-pro-regular - latin */\n@font-face {\n  font-display: swap; /* Check https://developer.mozilla.org/en-US/docs/Web/CSS/@font-face/font-display for other options. */\n  font-family: \"Be Vietnam Pro\";\n  font-style: normal;\n  font-weight: 400;\n  src:\n    url(\"../src/assets/fonts/be-vietnam-pro-v11-latin-regular.woff2\")\n      format(\"woff2\"),\n    /* Chrome 36+, Opera 23+, Firefox 39+, Safari 12+, iOS 10+ */\n      url(\"../src/assets/fonts/be-vietnam-pro-v11-latin-regular.woff\")\n      format(\"woff\"); /* Chrome 5+, Firefox 3.6+, IE 9+, Safari 5.1+, iOS 5+ */\n}\n/* be-vietnam-pro-500 - latin */\n@font-face {\n  font-display: swap; /* Check https://developer.mozilla.org/en-US/docs/Web/CSS/@font-face/font-display for other options. */\n  font-family: \"Be Vietnam Pro\";\n  font-style: normal;\n  font-weight: 500;\n  src:\n    url(\"../src/assets/fonts/be-vietnam-pro-v11-latin-500.woff2\")\n      format(\"woff2\"),\n    /* Chrome 36+, Opera 23+, Firefox 39+, Safari 12+, iOS 10+ */\n      url(\"../src/assets/fonts/be-vietnam-pro-v11-latin-500.woff\")\n      format(\"woff\"); /* Chrome 5+, Firefox 3.6+, IE 9+, Safari 5.1+, iOS 5+ */\n}\n/* be-vietnam-pro-700 - latin */\n@font-face {\n  font-display: swap; /* Check https://developer.mozilla.org/en-US/docs/Web/CSS/@font-face/font-display for other options. */\n  font-family: \"Be Vietnam Pro\";\n  font-style: normal;\n  font-weight: 700;\n  src:\n    url(\"../src/assets/fonts/be-vietnam-pro-v11-latin-700.woff2\")\n      format(\"woff2\"),\n    /* Chrome 36+, Opera 23+, Firefox 39+, Safari 12+, iOS 10+ */\n      url(\"../src/assets/fonts/be-vietnam-pro-v11-latin-700.woff\")\n      format(\"woff\"); /* Chrome 5+, Firefox 3.6+, IE 9+, Safari 5.1+, iOS 5+ */\n}\n\n/* ******************* */\n/*  COMPONENTS & UTILS */\n/* ******************* */\n@media only screen and (min-width: 0rem) {\n  .button-solid {\n    z-index: 1;\n    position: relative;\n    display: inline-block;\n    background-color: var(--color-primary);\n    width: auto;\n    padding: 1rem 2rem;\n    text-decoration: none;\n    font-size: 0.8125rem;\n    line-height: 1.4em;\n    font-weight: bold;\n    border-radius: 100vw;\n    overflow: hidden;\n    color: var(--color-body-text-light);\n    transition: color 0.3s;\n    transition-delay: 0.1s;\n    text-align: center;\n\n    &.inverted {\n      background-color: var(--color-body-text-light);\n      color: var(--color-primary);\n    }\n  }\n  .button-solid:hover {\n    color: #fff;\n  }\n  .button-solid:hover:before {\n    width: 100%;\n  }\n  .button-solid:before {\n    z-index: -1;\n    position: absolute;\n    top: 0;\n    left: 0;\n    content: \"\";\n    opacity: 1;\n    display: block;\n    background-color: var(--color-complementary);\n    height: 100%;\n    width: 0;\n    transition: width 0.3s;\n  }\n  .button-outline {\n    z-index: 1;\n    position: relative;\n    display: inline-block;\n    width: auto;\n    padding: 1rem 2rem;\n    text-decoration: none;\n    text-transform: uppercase;\n    font-size: 1rem;\n    line-height: 1em;\n    font-weight: bold;\n    border: 2px solid #000;\n    color: #000;\n    transition: color 0.3s;\n    transition-delay: 0.1s;\n    text-align: center;\n  }\n  .button-outline:hover {\n    color: #fff;\n  }\n  .button-outline:hover:before {\n    width: 100%;\n  }\n  .button-outline:before {\n    z-index: -1;\n    position: absolute;\n    top: 0;\n    left: 0;\n    content: \"\";\n    opacity: 1;\n    display: block;\n    background-color: #000;\n    height: 100%;\n    width: 0;\n    transition: width 0.3s;\n  }\n}\n\n/* ****** */\n/* HEADER */\n/* ****** */\nheader {\n  width: 100%;\n  position: relative;\n  padding: 0 1.5rem;\n  background-color: transparent;\n  margin-top: 2.5625rem;\n  z-index: 2;\n\n  .container {\n    width: 100%;\n    max-width: calc(1280 / 16 * 1rem);\n    margin: auto;\n    display: flex;\n    justify-content: flex-end;\n    align-items: center;\n    gap: calc(24 / 16 * 1rem);\n  }\n\n  .logo {\n    width: 40%;\n    max-width: calc(var(--header-logo-scale-factor) / 16 * 1rem);\n    height: 100%;\n    margin: 0 auto 0 0;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    z-index: 10;\n\n    img {\n      width: 100%;\n      height: 100%;\n      /* ensures the image never overflows the container. It stays contained within it's width and height and expands to fill it then stops once it reaches an edge */\n      object-fit: contain;\n      /* places the image to the left edge of the parent */\n      object-position: left;\n    }\n  }\n\n  nav ul {\n    width: auto;\n    min-width: 40%;\n    height: 65vh;\n    margin: 0;\n    padding: 3rem clamp(1.75rem, 3vw, 2.5rem) 2rem 4.375rem;\n    display: flex;\n    flex-direction: column;\n    justify-content: flex-start;\n    align-items: flex-end;\n    gap: 1.25rem;\n  }\n\n  nav li {\n    list-style: none;\n    margin-bottom: calc(16 / 16 * 1rem);\n    &:last-of-type {\n      margin-bottom: 0;\n    }\n    .li-link {\n      font-size: calc(16 / 16 * 1rem);\n      text-decoration: none;\n      line-height: 1.5em;\n      color: var(--color-header-text);\n      cursor: pointer;\n      display: inline-block;\n      position: relative;\n\n      &:before {\n        /* active state underline */\n        content: \"\";\n        width: 100%;\n        height: 1px;\n        background-color: currentColor;\n        opacity: 1;\n        display: none;\n        position: absolute;\n        bottom: calc(-2 / 16 * 1rem);\n        left: 0;\n      }\n\n      &.active {\n        &:before {\n          display: block;\n        }\n      }\n    }\n  }\n}\n\n/* ******************* */\n/*     MOBILE MENU     */\n/* ******************* */\n@media only screen and (max-width: 1023.5px) {\n  header {\n    nav .ul-wrapper {\n      height: 100vh;\n      background-color: #ffffff;\n      box-shadow: inset rgba(0, 0, 0, 0.2) 0px 8px 24px;\n      opacity: 0;\n      position: absolute;\n      top: 100%;\n      right: 0;\n      left: auto;\n      z-index: -1;\n      overflow: hidden;\n      transform: scaleX(0);\n      transition:\n        transform 0.4s,\n        opacity 0.3s;\n      transform-origin: top right;\n    }\n\n    nav ul {\n      overflow: scroll;\n    }\n\n    nav li {\n      text-align: right;\n      width: 100%;\n      opacity: 0;\n      transform: translateX(-2.5rem);\n      transition:\n        transform 0.6s,\n        opacity 0.9s;\n\n      .li-link {\n        font-size: clamp(1rem, 2.5vw, 1.5rem);\n        line-height: 1.2em;\n        display: inline-block;\n        position: relative;\n      }\n    }\n\n    nav li:nth-of-type(1) {\n      transition-delay: 0.05s;\n    }\n\n    nav li:nth-of-type(2) {\n      transition-delay: 0.1s;\n    }\n\n    nav li:nth-of-type(3) {\n      transition-delay: 0.15s;\n    }\n\n    nav li:nth-of-type(4) {\n      transition-delay: 0.2s;\n    }\n\n    nav li:nth-of-type(5) {\n      transition-delay: 0.25s;\n    }\n\n    nav li:nth-of-type(6) {\n      transition-delay: 0.3s;\n    }\n\n    nav li:nth-of-type(7) {\n      transition-delay: 0.35s;\n    }\n\n    nav li:nth-of-type(8) {\n      transition-delay: 0.4s;\n    }\n\n    nav li:nth-of-type(9) {\n      transition-delay: 0.45s;\n    }\n\n    .hamburger {\n      /* 44px - 48px */\n      width: clamp(2.75rem, 6vw, 3rem);\n      height: clamp(2.75rem, 6vw, 3rem);\n      margin: 0 0 0 auto;\n      background-color: transparent;\n      border: none;\n      border-radius: 0.25rem;\n      display: flex;\n      justify-content: center;\n      align-items: center;\n\n      .box {\n        /* 24px - 28px */\n        width: clamp(1.5rem, 2vw, 1.75rem);\n        /* 14px - 16px */\n        height: clamp(0.875rem, 1.5vw, 1rem);\n        position: relative;\n      }\n\n      .line {\n        width: 100%;\n        height: 4px;\n        background-color: var(--color-complementary);\n        transition: transform 0.5s ease;\n        display: block;\n        position: absolute;\n      }\n\n      .line1 {\n        top: 0;\n      }\n\n      .line2 {\n        top: 50%;\n      }\n\n      .line3 {\n        top: 100%;\n      }\n    }\n\n    .button-solid {\n      display: none;\n    }\n  }\n\n  header::before {\n    /* black blurred overlay */\n    content: \"\";\n    width: 0%;\n    height: 100vh;\n    background: rgba(0, 0, 0, 0.6);\n    opacity: 0;\n    display: block;\n    position: absolute;\n    top: 100%;\n    right: 0;\n    z-index: -11;\n    transition:\n      width 0.5s,\n      opacity 0.3s;\n    -webkit-backdrop-filter: blur(10px);\n    backdrop-filter: blur(10px);\n  }\n\n  header.active::before {\n    width: 100%;\n    opacity: 1;\n  }\n\n  header.active {\n    nav .ul-wrapper {\n      opacity: 1;\n      transform: scaleX(1);\n      transition-delay: 0.2s;\n    }\n\n    nav li {\n      opacity: 1;\n      transform: translateX(0);\n    }\n\n    .hamburger {\n      .line1 {\n        transform: rotate(45deg);\n        top: 50%;\n      }\n      .line2 {\n        transform: scale(0);\n      }\n      .line3 {\n        transform: rotate(-45deg);\n        top: 50%;\n      }\n    }\n  }\n}\n\n/* ******************* */\n/* NAVIGATION DROPDOWN */\n/* ******************* */\n@media only screen and (max-width: 1023.5px) {\n  header {\n    nav li.dropdown {\n      color: var(--color-body-text-light);\n      position: relative;\n\n      .li-link {\n        position: relative;\n      }\n\n      .drop-icon {\n        width: 0.9375rem;\n        height: auto;\n        position: absolute;\n        top: 50%;\n        right: -1.25rem;\n        transform: translateY(-50%);\n      }\n    }\n  }\n}\n\n@media only screen and (min-width: 64rem) {\n  header {\n    nav li.dropdown {\n      position: relative;\n      .li-link {\n        display: flex;\n        justify-content: center;\n        align-items: center;\n        gap: 0.2rem;\n      }\n    }\n\n    .drop-icon {\n      width: 1rem;\n      height: auto;\n      display: inline-block;\n    }\n  }\n}\n\n/* ******************* */\n/*       DESKTOP       */\n/* ******************* */\n@media only screen and (min-width: 64rem) {\n  header {\n    .container {\n      justify-content: space-between;\n    }\n\n    .logo {\n      margin-right: 0;\n    }\n\n    padding: 0 1rem;\n    .hamburger {\n      display: none;\n    }\n\n    nav ul {\n      width: 100%;\n      height: 100%;\n      padding: 0;\n      flex-direction: row;\n      justify-content: flex-start;\n      align-items: center;\n      gap: clamp(1.25rem, 2.6vw, 2.25rem);\n    }\n    nav li {\n      padding: 2rem 0;\n      margin: 0;\n\n      .li-link {\n        /* 14px - 16px */\n        font-size: clamp(0.875rem, 1vw, 1rem);\n        line-height: 1.5em;\n        text-decoration: none;\n        margin: 0;\n        color: var(--color-body-text);\n        display: block;\n        position: relative;\n\n        &:hover {\n          &:before {\n            width: 100%;\n          }\n        }\n\n        &.active {\n          &:before {\n            width: 100%;\n          }\n        }\n\n        &:before {\n          /* active state underline */\n          width: 0%;\n          height: 2px;\n          background: var(--color-primary);\n          display: block;\n          bottom: calc(0 / 16 * 1rem);\n          transit